import React, { Component } from 'react';

import LCC from "lightning-container";
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';


class App extends Component {

  onMessage(msg) {
    let name = msg.name;
    let value = msg.value;
    if (name === "Search") {
      this.callApex('getContacts', value);
    }
    else if (name === "Disable") {
      document.getElementById(msg.value).checked=false;
    }
    this.setState({messageReceived:value, messageReceivedName: name});
  }

  hideToast() {
    this.setState({messageReceived:'', messageReceivedName: '', errorMsg:''});
    
  }
  onMessageError(errMsg) {
    console.log(errMsg);
  }

  onMessageToSendChange(e) {
    this.setState({messageToSend: e.target.value});
  }

  sendMessage(name, msgtoSend) {
    LCC.sendMessage({name: name, value:msgtoSend});
  }

  callApex(method, value) {
    LCC.callApex("ReactApp_Controller_Login."+method,
                JSON.stringify(value),
                 this.handleAccountQueryResponse,
                 {escape: true});
  }

  onChangeAction(evt){

    if(evt.target.checked){

      this.sendMessage('ADD', evt.target.id);
    }else{
      this.sendMessage('REMOVE',  evt.target.id);
    }
  
  }

  allowEdit(e){
    let allowedEditList = this.state.allowedEditList;
    allowedEditList.push(e.target.id);

    this.setState({allowedEditList: allowedEditList});
    console.log( this.state.allowedEditList);
  
  }

  saveToDB(e) {
    var data= this.state.rows;
    for(let obj of data){
      if(obj.Id===e.target.id){
        console.log(obj);
        this.callApex('updateRecord',obj);
         break;
      }
    }
    console.log('AFT');
    let allowedEditList = this.state.allowedEditList;
    var index = allowedEditList.indexOf(e.target.id);
    if (index > -1) {
      allowedEditList.splice(index, 1);
    }

    this.setState({allowedEditList: allowedEditList});
    console.log( this.state.allowedEditList);

  }


  handleAccountQueryResponse(result, event) {
    console.log('AFT');
    console.log(result, event);
    if (event.status) {
      if(event.method==='getContacts'){
      this.setState({rows: result});
      }else if(event.method==='updateRecord'){
        console.log('Saved');
      }
    }
    else if (event.type === "exception") {
      this.setState({errorMsg:event.message + " : " + event.where});
      console.log(event.message + " : " + event.where);
    }
  }

  onEdit(e, id, field) {
    var data= this.state.rows;
    for(let obj of data){
      if(obj.Id===id){
        obj[field] =  e.target.value;
      }
    }
    this.setState({row: data });
  }

  render() {
    let toast = [];
    if(this.state.messageReceived&&this.state.messageReceived!==''){
    toast = [<div  key='tost' className='toastCustom'>Message from LCC: {this.state.messageReceivedName} <br/> {this.state.messageReceived}</div>];
    setInterval(this.hideToast, 5000);
    }
    if(this.state.errorMsg&&this.state.errorMsg!==''){
      toast = [<div  key='tost' className='toastError'>Error : <br/> {this.state.errorMsg}</div>];
      setInterval(this.hideToast, 7000);   
    }
    let columns = [<th key='#'>#</th>];
    let rows = [];

    for(let column of this.state.columns){

      columns.push(<th key={column.field} className="th-sm">{column.label}</th>);
    }
    for(let row of this.state.rows){
      row.selected = 'off';
      var btn
      if(this.state.allowedEditList.includes(row.Id)){
        btn = [<button className='btn btn-link' id={row.Id} key={row.Id+'btn'} onClick={this.saveToDB.bind(this)}>save</button>];
      }else{
        btn = [<button className='btn btn-link' id={row.Id} key={row.Id+'btn'} onClick={this.allowEdit.bind(this)}>edit</button>];
      }
       
      let local  =[<td  key={row.Id}><input type='checkbox'  id={row.Id}  onChange={this.onChangeAction.bind(this)} />{ btn} </td>];
      for(let column of this.state.columns){
        if(this.state.allowedEditList.includes(row.Id)){
          local.push(<td key={row.Id+'_'+column.field}><input value={ row[column.field]} onChange={e => this.onEdit(e, row.Id, column.field)}/></td>);
        }else{
          local.push(<td key={row.Id+'_'+column.field}>{ row[column.field]}</td>);
        }
        
      }
      rows.push(
      <tr key={row.Id+'#'}>
      {local}
    </tr>
      );
    }
 
    return (
      <div>
       {toast}
      <table id="dtOrderExample" className="table table-striped table-bordered table-sm" cellSpacing="0" width="80%">
      <thead>
        <tr>

       {columns}
        </tr>
      </thead>
      <tbody>
       {rows}
        </tbody>
        </table>
        {toast}
        </div>
    );
  }

  constructor(props) {
    super(props);
    this.state = {
      columns : [
        {field:'FirstName', label:'First Name'},
        {field:'LastName', label:'Last name'},
        {field:'Email', label:'Email'},
        {field:'Age__c', label:'Age'}

    ],
    rows : [ {Id:'s3s', Age__c:10, FirstName:'TEST', LastName:'fasewfaesd'} ,{Id:'ss2s', Age__c:10, FirstName:'TEST', LastName:'fasewfaesd'},{Id:'sss', Age__c:10, FirstName:'TEST', LastName:'fasewfaesd'}],
    pageNum:1,
    pageSize:20,
    messageReceived: "",
    messageReceivedName: "",
    messageToSend: "",
    search:'',
    errorMsg:'',
    allowedEditList:[]
    };

    this.sendMessage = this.sendMessage.bind(this);
    this.onMessageToSendChange = this.onMessageToSendChange.bind(this);
    this.onMessage = this.onMessage.bind(this);
    this.onMessageError = this.onMessageError.bind(this);

    this.handleAccountQueryResponse = this.handleAccountQueryResponse.bind(this);
    this.callApex = this.callApex.bind(this);
    this.hideToast = this.hideToast.bind(this);
    this.allowEdit = this.allowEdit.bind(this);
    
    this.saveToDB = this.saveToDB.bind(this);
    this.onEdit = this.onEdit.bind(this);
    this.callApex('getContacts','');
  }
  componentDidMount() {
    LCC.addMessageHandler(this.onMessage);
  }

  componentWillUnmount() {
    LCC.removeMessageHandler(this.oneMessage);
  }

  

}

export default App;



